.nav {
  display: flex;
  background-color: rgb(63, 63, 63);
  font-family: 'Roboto', sans-serif;
  justify-content: space-around;
  height: 60px;
}

.nav-title {
  color: white;
  margin-left: 15px;
}

.nav-links {
  color: white;
  list-style: none;
  display: flex;
  align-items: center;
  padding: 0px;
  gap: 20px;
}

.active {
  font-weight: 500;
}

.btn {
  color: white;
  background-color: cadetblue;
  margin-right: 15px;
  box-shadow: black 2px 2px;
}

a {
  color: lightgray;
  text-decoration: none;
}

/*******************************************/

#search_for_anything {
  height: 30px;
  width: 300px;
  border: 2px solid black;
  border-radius: 25px;
  margin-top: 10px;
}

#cart {
  height: 30px;
  margin-top: 10px;
}

/*******************************************/

h1 {
  display: flex;
  justify-content: center;
  height: 50px;
  background-color: black;
  font-size: xx-large;
}

h2 {
  height: 50px;
  width: 250px;
  background-color: rgb(148, 148, 148);
  border: 3px solid black;
}

.menuCards {
  display: flex;
  justify-content: space-around;
  margin-top: 100px;
}

#card {
  height: 200px;
}


#monsterCards, #spellCards, #trapCards, #shopNow {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}

#cardimg {
  height: 400px;
  width: 350px;
  background-color: coral;
}

.container {
  display: flex;
  justify-content: space-around;
}

#description {
  height: 400px;
  width: 250px;
  background-color: rgb(148, 148, 148);border: 3px solid black;
  /* flex-direction: column; */
  /* display: flex; */
  align-items: center;
  justify-content: space-around;
  font-size: x-large;
}

#price {
  height: 70px;
  width: 70px;
  background-color: rgb(148, 148, 148);
  border: 3px solid black;
  margin-top: 10px;
  margin-bottom: 10px;
}

#meme {
  display: flex;
  justify-content: center;
}


body {
  background-image: url("data:image/png;base64,");
  /* background-size: cover; */
  color: whitesmoke;
}

